`,
                "Exclusive Opportunity at RBJ Condominium": `Page 5
Thank you for your previous inquiry about RBJ, our newly launched condominium in Bukit Jalil. Are you considering it as an investment or for your home?
If you're looking for a place to live, RBJ is the perfect choice. Located just 5 minutes from Pavilion and the LRT, it offers an unbeatable combination of convenience and tranquility. Imagine living in a home where everything you need is just a few minutes away—shops, restaurants, schools, and easy transportation access.
Plus, with its serene surroundings and modern amenities, RBJ offers a high-quality lifestyle for both families and individuals. Whether you're thinking about upgrading your lifestyle or securing a valuable asset for the future, RBJ is a smart move.
`,
"coldcall":`Alvin (Agent):
"Hello, is Mr. Jones available, please?
My name is Alvin, and I'm a real estate agent. I'm calling about a potential property sale or rental at Greenpark Condominium.
I understand you may be the owner. Would you be interested in discussing this further?"

Mr. Jones (Owner):
"Hi Alvin, yes, I'm the owner. What exactly are you looking to discuss?"

Alvin (Agent):
"Thank you for confirming, Mr. Jones. I work with clients who are actively looking for properties in the Greenpark Condominium area, both for buying and renting.
I wanted to know if your unit is currently available, or if you'd consider listing it either for sale or rent? This could be a great opportunity given the demand in the area."

Mr. Jones (Owner):
"Well, I hadn't thought about selling, but I might be open to renting. Could you tell me more about what the market looks like?"

Alvin (Agent):
"Absolutely, Mr. Jones. The rental market in Greenpark is currently strong, especially due to its prime location. I can provide you with up-to-date rental rates and help find reliable tenants quickly. Would you prefer a long-term or short-term rental? And would you like me to send over a detailed market analysis for your unit?"

Mr. Jones (Owner):
"That sounds good. I'm more inclined towards long-term rentals. Please send me the details, and we can go from there."

Alvin (Agent):
"Fantastic! I’ll send over a market analysis along with the expected rental returns and any potential opportunities. I'll follow up in a few days to discuss the next steps. Feel free to reach out if you have any questions in the meantime."

Mr. Jones (Owner):
"Thanks, Alvin. I’ll look forward to the details."`
,"coldtalk2":`"Hello, is Mr. Jones available, please?
My name is Alvin, and I'm a real estate agent. I'm calling about a potential property sale or rental at Greenpark Condominium."

Caller (Not Mr. Jones):
"Hi, sorry, I'm not Mr. Jones. Can I help you with something?"

Alvin (Agent):
"Thank you for letting me know. By any chance, do you know if Mr. Jones is the owner of a unit at Greenpark Condominium?
I’m reaching out because I work with clients who are looking for properties in that area, and I wanted to see if his unit might be available for sale or rent."

Caller (Not Mr. Jones):
"I’m not sure about that. I’m just a tenant here."

Alvin (Agent):
"Got it, thanks for the clarification. If you have a way of reaching Mr. Jones, I’d appreciate it if you could pass along my message. Or, if it’s convenient, you could share his contact information with me, and I’ll follow up directly.

I work with clients interested in properties like this, so if there’s any interest in selling or renting, we could explore those options together."

Caller (Not Mr. Jones):
"I’ll pass along your message, but I’m not sure how interested he’d be."

Alvin (Agent):
"That’s all I ask. Thank you so much for your help! If Mr. Jones has any questions or is open to discussing it further, please feel free to share my number with him. Have a great day!"`

            ,
            "project":`嗨，Jacqie先生！我是Bukit Jalil的开发商代表，我从您对我们Facebook广告的询问中得到了您的联系方式。您是不是正在寻找Bukit Jalil的房子，
对吧？我们@project项目正好位于Bukit Jalil区域，价格大约600千。您今天有空来参观我们的展厅吗？我们特别为您准备了独家VIP优惠，
展厅今天开放到下午6点，机会难得，千万别错过哦！"`},
            formattedText: "",
        };
    },
    created() {
        const contactString = this.$route.query.contact;
        let contact = null;

        if (contactString) {
            try {
                contact = JSON.parse(contactString);
            } catch (e) {
                console.error('Failed to parse contact string:', e);
            }
        }

        let name = "";
        let project = "";

        if (contact) {
            name = contact.name;
            project = contact.project;

            if (project && name) {
                // Update formattedText based on selected title and contact details
                this.formattedText = this.buildPersonalizedText(project, name, this.selectedTitle);
            }
        }
    },
    watch: {
        // Watch for changes in the selected title to update the displayed message
        selectedTitle(newTitle) {
            const contactString = this.$route.query.contact;
            let contact = null;

            if (contactString) {
                try {
                    contact = JSON.parse(contactString);
                } catch (e) {
                    console.error('Failed to parse contact string:', e);
                }
            }

            if (contact) {
                this.formattedText = this.buildPersonalizedText(contact.project, contact.name, newTitle);
            }
        }
    },
    computed: {
        selectedMessage() {
            return this.formattedText; // Return the formatted text for display
        }
    },
    methods: {
        buildPersonalizedText(project, name, title) {
            // Modify the text to personalize based on project, name, and selected title
            let personalizedText = this.messages[title];
            personalizedText = personalizedText.replaceAll('@name', name); // Replace placeholder with name
            personalizedText = personalizedText.replaceAll('@project', project); // Replace placeholder with project
            return personalizedText;
        },
    },
};
</script>

<style scoped>
.full-height {
    height: 100vh;
}

p {
    white-space: pre-wrap;
    word-break: break-word;
    overflow-y: auto;
    max-height: 100%;
}
</style>
